<template>
  <div>
    <el-container style="border: 1px solid #eee;">
      <el-header height="30px" class="table-header">
        <el-row>
          <el-col :span="12">
            <span></span>
            <el-button type="text" icon="el-icon-circle-plus" @click="tmAddFunc" size="medium" title=""></el-button>
          </el-col>
        </el-row>
      </el-header>
      <el-main style="padding: 0px;">
        <el-table :data="tmTableData" ref="tmTable"
                  :span-method="groupHandle"
                  :row-class-name="groupClassName"
                  @row-click="toggleListHandle"
                  max-height="430"
                  style="width: 100%">
          <el-table-column label="">
            <template slot-scope="scope">
              <el-button type="text" icon="el-icon-edit" :disabled="getToolBtnVisible(scope.$index, scope.row)" title="" @click="tmEditFunc(scope.$index, scope.row)"></el-button>
              <el-button type="text" icon="el-icon-delete" :disabled="getToolBtnVisible(scope.$index, scope.row)" title="" @click="tmDelFunc(scope.$index, scope.row)"></el-button>
              <el-button type="text" icon="el-icon-search" title="" @click="tmInfoFunc(scope.$index, scope.row)"></el-button>
            </template>
          </el-table-column>
          <el-table-column label="" prop="name">
            <template slot-scope="scope">
              <i v-if="scope.row.group_type==='group'" :class=groupIcon(scope.row)></i>
              <span>{{scope.row.name}}</span>
            </template>
          </el-table-column>
          <el-table-column label="" prop="ref_name"></el-table-column>
          <el-table-column label="" prop="src_id"></el-table-column>
          <el-table-column label="" prop="type"></el-table-column>
          <el-table-column label="" prop="fd_name"></el-table-column>
          <el-table-column label="" prop="code" :formatter="formatterCode"></el-table-column>
          <el-table-column label="" prop="src_default"></el-table-column>
          <el-table-column label="" prop="genesisrul"></el-table-column>
        </el-table>
      </el-main>
    </el-container>
    <el-dialog :title="tmTitle" :visible.sync="tmDialogVisible" @open="openTmDialog">
      <el-form :model="tmForm"  :label-width="formLabelWidth" :rules="tmRules" ref="tmForm">
        <el-form-item label="" prop="tab_name" class="hidden">
          <el-input v-model="tmForm.tab_name" auto-complete="off"></el-input>
        </el-form-item>
        <el-row>
          <el-col :span="12">
            <el-form-item label="" prop="name">
              <el-input v-model="tmForm.name" :disabled="tmFormReadOnly" auto-complete="off"></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="" prop="ref_name">
              <el-input v-model="tmForm.ref_name" :disabled="tmFormReadOnly" auto-complete="off"></el-input>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="12">
            <el-form-item label="" prop="src_id">
              <el-input v-model="tmForm.src_id" :disabled="tmFormReadOnly" auto-complete="off"></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="" prop="type">
              <el-select v-model="tmForm.type" :disabled="tmFormReadOnly" placeholder="" @change="tmTypeChange">
                <el-option
                  v-for="item in tmTypeList"
                  :key="item.value"
                  :label="item.label"
                  :value="item.value">
                </el-option>
              </el-select>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="12">
            <el-form-item label="" prop="fd_name">
              <el-select v-model="tmForm.fd_name" :disabled="tmFormReadOnly" placeholder="">
                <el-option
                  v-for="item in tmFdNameList"
                  :key="item.fd_name"
                  :label="item.fd_name"
                  :value="item.fd_name">
                </el-option>
              </el-select>
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="" prop="code" :disabled="tmFormReadOnly" v-bind:class="{hidden:tmCodeVisible}">
              <el-select v-model="tmForm.code" placeholder="">
                <el-option
                  v-for="item in tmCodeList"
                  :key="item.category_id"
                  :label="item.category_name"
                  :value="item.category_id">
                </el-option>
              </el-select>
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="" prop="genesisrul" :disabled="tmFormReadOnly" v-bind:class="{hidden:tmFuncVisible}">
              <el-select v-model="tmForm.genesisrul" placeholder="">
                <el-option
                  v-for="item in tmFuncList"
                  :key="item.value"
                  :label="item.label"
                  :value="item.value">
                </el-option>
              </el-select>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="12">
            <el-form-item label="1" prop="params1" :disabled="tmFormReadOnly" v-bind:class="{hidden:paramsVisible(1)}">
              <el-input v-model="tmForm.params1" auto-complete="off"></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="2" prop="params2" :disabled="tmFormReadOnly" v-bind:class="{hidden:paramsVisible(2)}">
              <el-input v-model="tmForm.params2" auto-complete="off"></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label=""  prop="src_default" :disabled="tmFormReadOnly">
              <el-select v-if="tmForm.type.toUpperCase() === 'CODE'" v-model="tmForm.src_default" placeholder="">
                <el-option
                  v-for="item in codeDefaultList"
                  :key="item.value"
                  :label="item.label"
                  :value="item.value">
                </el-option>
              </el-select>
              <el-input v-else v-model="tmForm.src_default" :disabled="tmFormReadOnly" auto-complete="off"></el-input>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="24">
            <el-form-item>
              <el-button type="primary" @click="submitTmForm" size="large"></el-button>
              <el-button @click="tmDialogVisible = false" size="large"></el-button>
            </el-form-item>
          </el-col>
        </el-row>
      </el-form>
    </el-dialog>
    <el-container style="border: 1px solid #eee;">
      <el-header height="30px" class="table-header">
        <div></div>
      </el-header>
      <el-main style="padding: 0px;">
        <div style="padding-left: 10px;text-align: left;border-bottom: 1px solid #eee;">
          <el-button type="text" class="el-icon-plus" @click=""></el-button>
          <el-button type="text" class="el-icon-edit" @click=""></el-button>
          <el-button type="text" class="el-icon-delete" @click=""></el-button>
          <el-button type="text" class="el-icon-plus" @click=""></el-button>
          <el-button type="text" class="el-icon-edit" @click=""></el-button>
          <el-button type="text" class="el-icon-delete" @click=""></el-button>
          <el-button type="text" class="el-icon-search" @click=""></el-button>
        </div>
        <el-table :data="refTableData" ref="refTable"
                  :span-method="groupHandle"
                  :row-class-name="groupClassName"
                  @row-dblclick="toggleListHandle"
                  max-height="430"
                  style="width: 100%">
          <el-table-column type="selection" width="35" align="left"></el-table-column>
          <el-table-column label="" prop="ref_desc">
            <template slot-scope="scope">
              <i v-if="scope.row.group_type==='group'" :class=groupIcon(scope.row)></i>
              <span style="margin-left: 10px">{{scope.row.ref_desc}}</span>
            </template>
          </el-table-column>
          <el-table-column label="" prop="ref_name"></el-table-column>
          <el-table-column label="" prop="ref_fd_name"></el-table-column>
          <el-table-column label="" prop="src_cond_in"></el-table-column>
          <el-table-column label="" prop="src_expr_in"></el-table-column>
          <el-table-column label="" prop="storecolumn"></el-table-column>
          <el-table-column label="" prop="tab_desc"></el-table-column>
        </el-table>
      </el-main>
    </el-container>
  </div>
</template>

<script>
  import ajax from '@/common/ajax'
  import check from '@/common/check'
  import util from '@/common/util'
  import dictCode from '@/common/dictCode'

  let vm = null
  export default {
    create () {

    },
    computed: {
      txnIdParent () { return this.txnId }, // 
      isVisibilityParent () { return this.isVisibility }, // tab
      tmTableData () { // Table
        var expendNodeKey = this.expendNodeKey
        var tranModelList = this.tranModelList
        var tableData = []
        for (var i in tranModelList) {              // 
          for (var key in tranModelList[i]) {       // Key-Value
            // List0tranModelList
            if (tranModelList[i][key].length > 0 || Number(i) === tranModelList.length - 1) {
              let keys = key.split('____')          // 
              if (keys && keys.length === 2) {      // 
                tableData.push({                    // 
                  tab_name: keys[0],
                  name: keys[1],
                  group_type: 'group'               // [group]
                })
                if (expendNodeKey.indexOf(key) === -1) {  // 
                  break                                   // 
                }
                for (var j in tranModelList[i][key]) {    // 
                  var item = tranModelList[i][key][j]
                  item.group_type = 'item'                // [item]
                  tableData.push(item)
                }
              }
            }
          }
        }
        return tableData
      },
      tmFdNameList () { // 
        let self = this
        let dataType = self.tmForm.type
        let enableStoreFd = self.enableStoreFd
        let dataTypeClassify = self.dataTypeClassify
        let fdName = self.tmForm.fd_name
        let hasFdName = false
        var sfdItems = []
        if (dataType && enableStoreFd) {
          for (let i in enableStoreFd) {
            let fd = enableStoreFd[i]
            for (let d in dataTypeClassify) {
              let dt = dataTypeClassify[d]
              if (dt.type.indexOf(dataType) !== -1) {
                if (dt.recap === 'long' || dt.recap === 'decimal') {
                  if (dataType !== 'double' && dataType !== 'long') {
                    if (fd['type'] === dataType) {
                      sfdItems.push(fd)
                      hasFdName = hasFdName || fd.fd_name === fdName
                    }
                  }
                  if (fd['type'] === 'double' || fd['type'] === 'long') {
                    sfdItems.push(fd)
                    hasFdName = hasFdName || fd.fd_name === fdName
                  }
                } else if (dt.recap === 'string') {
                  if (dataType !== 'string') {
                    if (fd['type'] === dataType) {
                      sfdItems.push(fd)
                      hasFdName = hasFdName || fd.fd_name === fdName
                    }
                  }
                }
                if (fd['type'] === 'string') {
                  sfdItems.push(fd)
                  hasFdName = hasFdName || fd.fd_name === fdName
                }
              }
            }
          }
        }
        // 
        if (!hasFdName && fdName !== '') {
          let allStoreFd = self.allStoreFd
          for (let i in allStoreFd) {
            if (allStoreFd[i].fd_name === fdName) {
              sfdItems.splice(0, 0, allStoreFd[i])
            }
          }
        }
        return sfdItems
      },
      codeDefaultList () {
        let type = this.tmForm.type
        if (type !== 'code') {
          return []
        }
        let code = this.tmForm.code
        this.tmForm.src_default = ''
        return dictCode.getCodeItems(code)
      },
      tmCodeVisible () { // 
        let type = this.tmForm.type
        return !type || type.toUpperCase() !== 'CODE'
      },
      tmFuncVisible () { // 
        return this.tmFuncList.length === 0
      },
      tmFuncList () { // 
        let type = this.tmForm.type
        let func = this.func
        let list = []
        for (let i in func) {
          if (func[i].func_type === type) {
            list.push({
              label: func[i].func_name,
              value: func[i].func_code
            })
          }
        }
        return list
      }
    },
    props: ['txnId', 'isVisibility'], // 
    data () {
      // 
      let codeCheck = (rule, value, callback) => {
        var self = this
        if (value.trim() === '' && self.tmForm.type.toUpperCase() === 'CODE') {
          return callback(new Error(''))
        } else {
          return callback()
        }
      }
      // 
      let tmFdNameCheck = (rule, value, callback) => {
        let self = this
        let list = self.tmFdNameList
        for (let i in list) {
          if (list[i].fd_name === value) {
            return callback()
          }
        }
        return callback(new Error(''))
      }
      // 
      let genesisrulCheck = (rule, value, callback) => {
        if (util.trim(value) === '') {
          return callback()
        }
        let self = this
        let list = self.tmFuncList
        for (let i in list) {
          if (list[i].value === value) {
            return callback()
          }
        }
        return callback(new Error(''))
      }
      // 1
      let paramsCheck1 = (rule, value, callback) => {
        let self = this
        if (self.paramsVisible(1)) { // 
          return callback()
        }
        if (util.trim(value) === '') {
          return callback(new Error(''))
        }
        let funcParam = self.funcParam // 
        let paramsList = [] // 
        for (let i in funcParam) {
          if (funcParam[i].func_code === self.tmForm.genesisrul) {
            paramsList.push(funcParam[i])
          }
        }
        if (paramsList.length === 0) {
          return callback()
        }
        paramsList = util.orderList(paramsList, 'param_orderby') // 
        let dataTypeClassify = self.dataTypeClassify
        let paramType = paramsList[0].param_type
        for (let i in dataTypeClassify) {
          let dt = dataTypeClassify[i]
          if (dt.type.indexOf(paramType) > -1) {
            if (dt.recap === 'long' || dt.recap === 'decimal') {
              if (dt.recap === 'long' && !/^[0-9]*$/.test(value)) {
                return callback(new Error('1'))
              }
              if (dt.recap === 'decimal' && !/^\d+(\.\d+)?$/.test(value)) {
                return callback(new Error('1'))
              }
              if ((value * 1 + '') !== (value + '')) {
                return callback(new Error('1')) // 0
              }
              if (self.tmForm.params2 !== '') {
                let params1 = Number(value)
                let params2 = Number(self.tmForm.params2)
                if (params2 < params1) {
                  return callback(new Error('12')) // 
                }
              }
            } else if (dt.recap === 'string') {
              if (!(value.charAt(0) === '"' && value.charAt(value.length - 1) === '"')) {
                return callback(new Error('["\' + value + \'"]'))
              }
            }
          }
        }
        callback()
      }
      // 2
      let paramsCheck2 = (rule, value, callback) => {
        let self = this
        if (self.paramsVisible(2)) { // 
          return callback()
        }
        if (util.trim(value) === '') {
          return callback(new Error(''))
        }
        let funcParam = self.funcParam // 
        let paramsList = [] // 
        for (let i in funcParam) {
          if (funcParam[i].func_code === self.tmForm.genesisrul) {
            paramsList.push(funcParam[i])
          }
        }
        if (paramsList.length === 0) {
          return callback()
        }
        paramsList = util.orderList(paramsList, 'param_orderby') // 
        let dataTypeClassify = self.dataTypeClassify
        let paramType = paramsList[0].param_type
        for (let i in dataTypeClassify) {
          let dt = dataTypeClassify[i]
          if (dt.type.indexOf(paramType) > -1) {
            if (dt.recap === 'long' || dt.recap === 'decimal') {
              if (dt.recap === 'long' && !/^[0-9]*$/.test(value)) {
                return callback(new Error('1'))
              }
              if (dt.recap === 'decimal' && !/^\d+(\.\d+)?$/.test(value)) {
                return callback(new Error('2'))
              }
              if ((value * 1 + '') !== (value + '')) {
                return callback(new Error('2')) // 0
              }
              if (self.tmForm.params1 !== '') {
                let params1 = Number(self.tmForm.params1)
                let params2 = Number(value)
                if (params2 < params1) {
                  return callback(new Error('21')) // 
                }
              }
            } else if (dt.recap === 'string') {
              if (!(value.charAt(0) === '"' && value.charAt(value.length - 1) === '"')) {
                return callback(new Error('["\' + value + \'"]'))
              }
            }
          }
        }
        callback()
      }
      return {
        op: '',
        toggleIcon: ['el-icon-caret-bottom', 'el-icon-caret-right'], // Table[]Icon
        tranModelList: [],      // 
        expendNodeKey: [],      // 
        tmTitle: '',            // 
        tmDialogVisible: false, // 
        tmFormReadOnly: true,   // 
        selectTmRows: [],
        tmForm: {
          params1: '',
          params2: '',
          tab_name: '',
          name: '',
          ref_name: '',
          src_id: '',
          type: '',
          fd_name: '',
          code: '',
          src_default: '',
          genesisrul: ''
        },             // 
        tmTypeList: [],         // -
        allStoreFd: [],         // 
        enableStoreFd: [],      // 
        tmCodeList: [],         // 
        refTab: [],             // 
        refFd: [],              // 
        canRefTab: [],          // 
        canRefTabFd: [],        // 
        func: [],               // 
        funcParam: [],          // 
        refTableData: [],       // 
        dataTypeClassify: [     // 
          {recap: 'long', type: ['long', 'time', 'datetime']},
          {recap: 'decimal', type: ['double', 'money']},
          {recap: 'string', type: ['string', 'devid', 'ip', 'userid', 'acc', 'code']}
        ],
        formLabelWidth: '120px',
        tmRules: {
          name: [
            {required: true, message: '', trigger: 'blur'},
            {validator: check.checkFormZhSpecialCharacter, trigger: 'blur'}, // ,,
            {max: 50, message: '20', trigger: 'blur'}
          ],
          ref_name: [
            {required: true, message: '', trigger: 'blur'},
            {validator: check.checkFormEnSpecialCharacter, trigger: 'blur'}, // ,
            {max: 50, message: '20', trigger: 'blur'}
          ],
          src_id: [
            {required: true, message: '', trigger: 'blur'},
            {validator: check.checkFormEnSpecialCharacter, trigger: 'blur'}, // ,
            {max: 20, message: '20', trigger: 'blur'}
          ],
          type: [
            {required: true, message: '', trigger: 'blur'},
            {validator: check.checkFormEnSpecialCharacter, trigger: 'blur'}, // ,
            {max: 20, message: '20', trigger: 'blur'}
          ],
          fd_name: [
            {required: true, message: '', trigger: 'blur'},
            {validator: check.checkFormEnSpecialCharacter, trigger: 'blur'}, // ,
            {validator: tmFdNameCheck, trigger: 'blur'}, // 
            {max: 50, message: '20', trigger: 'blur'}
          ],
          code: [
            {validator: check.checkFormSpecialCode, trigger: 'blur'}, // 
            {validator: codeCheck, trigger: 'blur'}, // 
            {max: 50, message: '20', trigger: 'blur'}
          ],
          src_default: [
            {validator: check.checkFormSpecialCode, trigger: 'blur'}, // ,
            {max: 50, message: '20', trigger: 'blur'}
          ],
          genesisrul: [
            {validator: genesisrulCheck, trigger: 'blur'} // 
          ],
          params1: [
            {max: 20, message: '20', trigger: 'blur'},
            {validator: paramsCheck1, trigger: 'blur'}
          ],
          params2: [
            {max: 20, message: '20', trigger: 'blur'},
            {validator: paramsCheck2, trigger: 'blur'}
          ]
        }
      }
    },
    mounted: function () {
      this.$nextTick(function () {
        vm = this
      })
    },
    watch: {
      txnId: {
        handler: (val, oldVal) => {
          if (vm.isVisibilityParent === true) {
            vm.initForm()
          }
        }
      },
      isVisibility: {
        handler: (val, oldVal) => {
          if (vm.isVisibilityParent === true) {
            vm.initForm()
          }
        }
      }
    },
    methods: {
      initForm () { // 
        var self = this
        ajax.post({
          url: '/tranmdl/query', // 
          param: {
            tab_name: self.txnId
          },
          success: function (data) {
            var tranModelList = data.txnfds
            if (tranModelList) {
              self.tranModelList = tranModelList  // 
              let expendNodeKey = []
              for (let i in tranModelList) {
                for (let key in tranModelList[i]) {
                  expendNodeKey.push(key)         // 
                }
              }
              self.expendNodeKey = expendNodeKey      // 
              self.refTab = data.txn_ref_tab          // 
              self.refFd = data.txn_ref_fds           // 
              self.enableStoreFd = data.enablestorefd // 
              self.allStoreFd = data.allstorefd       // 
            }
            ajax.post({
              url: '/tranmdl/queryOf', // 
              param: {
                tab_name: self.txnId
              },
              success: function (data) {
                self.tmCodeList = data.sourcetype // 
                self.canRefTab = data.canreftable // 
                self.canRefTabFd = data.reftblfd  // 
                self.func = data.func             // 
                self.funcParam = data.funcparam   // 
              }
            })
          }
        })
      },
      groupHandle ({ row, column, rowIndex, columnIndex }) {
        if (row.group_type === 'group') { // [group_type][group]
          switch (columnIndex) {          // 
            case 1 :        // 
              return [1, 9] // 
            default :       // 
              return [0, 0] // 
          }
        }
      },
      groupClassName ({row, rowIndex}) { // []
        if (row.group_type === 'group') {
          return 'groupStyle'
        } else {
          return ''
        }
      },
      groupIcon (row) {  // Icon
        if (row.group_type !== 'group') { // 
          return ''                       // 
        }
        let expendNodeKey = this.expendNodeKey      // 
        let key = row.tab_name + '____' + row.name  // 
        if (expendNodeKey.indexOf(key) > -1) {      // 
          return this.toggleIcon[0]
        } else {                                    // 
          return this.toggleIcon[1]
        }
      },
      toggleListHandle (row, e) {   // 
        let self = this
        if (row.group_type !== 'group') { // []
          return
        }
        let path = e.path
        let cellItem = null   // 
        for (let i in path) { // td[cell]
          if (path[i].className === 'cell') {
            cellItem = path[i]
          }
        }
        let key = row.tab_name + '____' + row.name // []
        let expendNodeKey = self.expendNodeKey     // []
        let toggleIcon = self.toggleIcon     // []
        let iconItem = cellItem.getElementsByTagName('i') // I
        if (iconItem[0].getAttribute('class') === toggleIcon[0]) { // []
          if (expendNodeKey.indexOf(key) > -1) {
            expendNodeKey.splice(expendNodeKey.indexOf(key), 1)
          }
        } else if (iconItem[0].getAttribute('class') === toggleIcon[1]) { // 
          expendNodeKey.push(key)
        }
        self.expendNodeKey = expendNodeKey
      },
      formatterCode (row, column) {  // table
        let self = this
        if (row.code === undefined || row.code === null || row.code === '') {
          return ''
        } else {
          let codeList = self.enableStoreFd
          for (let i in codeList) {
            if (codeList[i].category_id === row.code) {
              return codeList[i].category_name
            }
          }
          return row.code
        }
      },
      paramsVisible (index) {
        let self = this
        let funcList = self.func
        let code = self.tmForm.genesisrul
        for (let i in funcList) {
          if (funcList[i].func_code === code) {
            var params = funcList[i].params
            if (!(params === undefined || params === '')) {
              let visible = params.length < index
              if (visible) {
                self.tmForm['params' + index] = ''
              }
              return visible
            }
          }
        }
        self.tmForm['params' + index] = ''
        return true
      },
      getToolBtnVisible (index, row) { // 
        if (row.tab_name === this.txnId && (row.is_sys + '') !== '1') {
          return false
        }
        return true
      },
      tmTypeChange () {
        this.tmForm.src_default = ''
        this.tmForm.fd_name = ''
      },
      openTmDialog () { // 
        this.tmTypeList = dictCode.getCodeItems('tms.model.datatype') // 
        this.getFuncList()
        if (this.$refs['tmForm']) { // Formundefined
          this.$refs['tmForm'].resetFields()
        }
      },
      getTmFdNameList (tabName) { // 
        let self = this
        ajax.post({
          url: '/tranmdl/queryAvailableStoreFd',
          param: {
            tab_name: (tabName?tabName:self.txnId)
          },
          success: function (data) {
            self.enableStoreFd = data.enableStoreFd // 
          }
        })
      },
      getFuncList () {
        let self = this
        ajax.post({
          url: '/tranmdl/queryFunWithParam',
          success: function (data) {
            self.func = data.funwithparam
          }
        })
      },
      tmAddFunc () {  // 
        let self = this
        self.tmTitle = ''
        self.tmDialogVisible = true
        self.tmFormReadOnly = false
        self.op = 'add'
        self.tmForm = {
          params1: '',
          params2: '',
          tab_name: self.txnId,
          name: '',
          ref_name: '',
          src_id: '',
          type: '',
          fd_name: '',
          code: '',
          src_default: '',
          genesisrul: ''
        }
      },
      tmEditFunc (index, row) { // 
        let self = this
        self.tmTitle = ''
        self.tmDialogVisible = true
        self.tmFormReadOnly = false
        self.op = 'mod'
        let genesisrul = ''
        let params1 = ''
        let params2 = ''
        let genesisrulStr = row.genesisrul
        if (util.trim(genesisrulStr) !== '') {
          genesisrul = genesisrulStr.substring(0, genesisrulStr.indexOf('('))
          let params = genesisrulStr.substring(genesisrulStr.indexOf('(') + 1, genesisrulStr.indexOf(')')).split(',')
          if (params.length > 1) {
            params1 = params[1]
          }
          if (params.length > 2) {
            params2 = params[2]
          }
        }
        self.tmForm = {
          params1: params1,
          params2: params2,
          tab_name: row.tab_name,
          name: row.name,
          ref_name: row.ref_name,
          src_id: row.src_id,
          type: row.type,
          fd_name: row.fd_name,
          code: row.code,
          src_default: row.src_default,
          genesisrul: genesisrul
        }
        let timer = window.setInterval(function () {
          if (self.tmForm.params1 === params1 && self.tmForm.params2 === params2) {
            window.clearInterval(timer)
          } else {
            if (self.tmForm.params1 !== params1) {
              self.tmForm.params1 = params1
            } else {
              self.tmForm.params2 = params2
            }
          }
        }, 100)
        self.selectTmRows = [row]
      },
      submitTmForm () {
        let self = this
        this.$refs['tmForm'].validate((valid) => {
          if (valid) {
            let op = self.op
            let rowData = self.selectTmRows[0]
            let formData = self.tmForm
            let row = {}
            if (op === 'mod') {
              if (rowData.type !== formData.type && rowData.link.length > 0) {
                this.$confirm('', '', {
                  confirmButtonText: '',
                  cancelButtonText: '',
                  type: 'warning'
                }).then(() => {
                  rowData.link = ''
                }).catch(() => {
                  return false
                })
              }
              row = Object.assign({}, rowData, formData)
              row = Object.assign(row, {old: rowData})
            } else {
              row = Object.assign(row, formData)
            }
            let genesisrul = ''
            if (util.trim(row.genesisrul) !== '') {
              genesisrul = util.trim(row.genesisrul) + '(?'
              if (util.trim(row.params1) !== '') {
                genesisrul = genesisrul + ',' + row.params1
                if (util.trim(row.params2) !== '') {
                  genesisrul = genesisrul + ',' + row.params2
                }
              }
            }
            row.genesisrul = genesisrul + ')'
            let jsonData = {}
            jsonData[op] = [row]
            ajax.post({
              url: '/tranmdl/saveModel',
              param: {
                postData: jsonData
              },
              success: function (data) {
                self.$message.success('')
                self.initForm()
                self.tmDialogVisible = false
              }
            })
          } else {
            this.$message('')
            return false
          }
        })
      },
      tmDelFunc (index, row) { // 
        let self = this
        this.$confirm('', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          let jsonData = {'del': [row]}
          ajax.post({
            url: '/tranmdl/saveModel',
            param: {
              postData: jsonData
            },
            success: function (data) {
              self.$message.success('')
              self.initForm()
              self.tmDialogVisible = false
            }
          })
        }).catch(() => {
          return false
        })
      },
      tmInfoFunc (index, row) { // 
        let self = this
        self.tmTitle = ''
        self.tmDialogVisible = true
        self.tmFormReadOnly = true
        self.op = 'info'
        let genesisrul = ''
        let params1 = ''
        let params2 = ''
        let genesisrulStr = row.genesisrul
        if (util.trim(genesisrulStr) !== '') {
          genesisrul = genesisrulStr.substring(0, genesisrulStr.indexOf('('))
          let params = genesisrulStr.substring(genesisrulStr.indexOf('(') + 1, genesisrulStr.indexOf(')'))
          if (params.length > 1) {
            params1 = params[1]
          }
          if (params.length > 2) {
            params2 = params[2]
          }
        }
        self.tmForm = {
          params1: params1,
          params2: params2,
          tab_name: row.tab_name,
          name: row.name,
          ref_name: row.ref_name,
          src_id: row.src_id,
          type: row.type,
          fd_name: row.fd_name,
          code: row.code,
          src_default: row.src_default,
          genesisrul: genesisrul
        }
      }
    }
  }
</script>

<style>
  .hidden {
    display: none;
  }
  tbody .groupStyle {
    background-color: #f5f7fa !important;
    text-align: left;
  }
  tbody .groupStyle .cell {
    padding-left: 15px;
  }

  .table-header {
    text-align: left;
    line-height: 2em;
    background-color: aquamarine;
  }
</style>



